import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { CartHeadAction, ColEditDelete } from 'components/common';
import { BtnAddNew, BtnExport } from 'components/common/button';
import { Card, Row, Table, Col, message, Spin } from 'antd';
import { getBuildingRooms, deleteBuildingRoom } from 'services/buildingRoom';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';

import './style.scss';

const ROUTER_FORM_DATA = routeConstant.DANH_MUC__CO_SO_DAO_TAO__PHONG_HOC__NHAP_DU_LIEU;

class ClassroomPage extends Component {
  state = {
    data: [],
    loading: false,
  };

  componentWillMount = async () => {
    this.setState({ loading: true });
    const result = await getBuildingRooms();
    if (result.data) {
      this.setState({ data: result.data.items });
    }
    this.setState({ loading: false });
  };

  handleDelete = async id => {
    await deleteBuildingRoom(id);
    message.success('Xóa thành công');
    const result = await getBuildingRooms();
    if (result.data) {
      this.setState({ data: result.data.items });
    }
  };

  expandedRoomRowRender = floor => {
    const innerColumns = [
      {
        title: 'Ký hiệu phòng',
        dataIndex: 'label',
        key: 'label',
        className: 'column-room-label',
      },
      {
        title: 'Sức chứa',
        dataIndex: 'size',
        key: 'size',
      },
      {
        title: 'Loại phòng',
        dataIndex: 'roomType',
        key: 'roomType',
      },
      {
        title: 'Thiết bị',
        dataIndex: 'devices',
        key: 'devices',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        key: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: ROUTER_FORM_DATA,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];

    return (
      <Table
        columns={innerColumns}
        dataSource={floor.roomList || []}
        pagination={false}
        className="room-table"
      />
    );
  };

  expandedFloorRowRender = buildingBase => {
    const innerColumns = [
      {
        title: 'Tầng',
        dataIndex: 'buildingFloor',
        key: 'buildingFloor',
        className: 'building-floor-column',
      },
      { title: 'SL phòng', dataIndex: 'noOfRoom', key: 'noOfRoom' },
    ];

    return (
      <Table
        columns={innerColumns}
        dataSource={buildingBase.floorList || []}
        pagination={false}
        expandedRowRender={this.expandedRoomRowRender}
        className="floor-table"
      />
    );
  };

  render() {
    const outerColumns = [
      {
        title: 'Cơ sở đào tạo',
        dataIndex: 'buildingBase',
        key: 'buildingBase',
        className: 'building-base',
      },
      { title: 'Tòa nhà', dataIndex: 'building', key: 'building' },
      { title: 'SL tầng', dataIndex: 'noOfFloor', key: 'noOfFloor' },
    ];
    const { loading } = this.state;

    return (
      <Card title="Danh mục phòng học">
        <CartHeadAction>
          <Link to={ROUTER_FORM_DATA}>
            <BtnAddNew />
          </Link>
          <BtnExport className="btnExport" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <Spin spinning={loading}>
              <Table
                className="building-table"
                columns={outerColumns}
                expandedRowRender={this.expandedFloorRowRender}
                dataSource={this.state.data || []}
                bordered
              />
            </Spin>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassroomPage />
  </Page>
);



